@keyframes slide-top {
  from {
    transform: scale(.9);
  }

  to {
    transform: scale(1.1);
  }
}

@keyframes scaler {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

.scroll-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--col-btn-bg-default-primary);
  cursor: pointer;
  z-index: 10;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: slide-top .75s linear, scaler 1.5s infinite alternate both;
  display: none;
  position: fixed;
  bottom: 5vh;
  right: 30px;
}

.arrow-top {
  fill: #fff;
}

.btn {
  width: fit-content;
  text-transform: uppercase;
  height: fit-content;
  color: var(--col-btn-default);
  background-color: var(--col-btn-bg-default-primary);
  transition: color var(--animation-time) var(--cubic-function), background-color var(--animation-time) var(--cubic-function);
  border-style: none;
  border-radius: 6px;
  align-items: center;
  padding: 18px 32px 16px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 900;
  line-height: 1;
  display: flex;
}

.btn:hover {
  color: var(--col-btn-hover-light);
  background-color: var(--col-btn-bg-hover);
}

.btn:focus {
  color: var(--col-btn-focus);
  background-color: var(--col-btn-bg-focus);
}

.btn:disabled {
  color: var(--col-btn-disabled);
  background-color: var(--col-btn-bg-disabled);
  cursor: inherit;
}

.btn--transparent {
  height: 42px;
  transition: color var(--animation-time) var(--cubic-function), border var(--animation-time) var(--cubic-function);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.btn--transparent:hover {
  color: var(--col-btn-hover);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0a8817;
}

.btn--transparent:focus {
  color: var(--col-btn-focus);
  fill: var(--col-btn-focus);
  background-color: rgba(0, 0, 0, 0);
}

.section-title {
  text-align: center;
